/* Hero-Section */

.home {
  padding: 1em;
  background-color: #fff;
  margin: 2em;
  box-shadow: 0 0.4em 0.7em var(--background-color);
  border-radius: 0.3em;
}

/* section.hero-section {

} */

div.hero-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  height: 70vh;
}

.hero-img-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.hero-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.hero-text-container,
.hero-social-links-container {
  position: relative;
  z-index: 1;
  text-align: center;
}

.hero-text {
  color: #fff;
  -webkit-text-stroke: 0.03em;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0.3em 0.6em #111;
  transition: -webkit-text-fill-color, var(--slow-transition) linear;
  cursor: default;
  width: max(70%);
  margin: auto;
}

.hero-text:hover {
  -webkit-text-fill-color: #fff;
}

.links-list {
  display: flex;
  gap: 2em;
}

.links-list li {
  margin-top: 1em;
}

.links-list li a {
  color: #fff;
  transition: transform,
    var(--normal-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.links-list li a:hover {
  transform: translateY(-0.6em);
  display: inline-block;
}

div.hero-footer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-footer-right {
  width: 15em;
  aspect-ratio: 16 / 9;
  z-index: 1;
  position: absolute;
  right: -2em;
  bottom: -2em;
  transform: rotate(-8deg);
  border: 0.1em solid black;
}

.hero-footer-left {
  width: 15em;
  aspect-ratio: 16 / 9;
  z-index: 1;
  position: absolute;
  left: -2em;
  top: 8em;
  transform: rotate(8deg);
  border: 0.1em solid black;
}

/* Founder-section */
section.founder-section {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  width: max(90%, 80%);
  margin: auto;
  gap: 2em;
  background-image: url("../../images/about1.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.section.founder-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../../images/founder.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.3;
  z-index: -1;
}

/* div.founder-img-container {
  width: max(90%, 80%);
  margin: auto;
} */

.founder-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.founder-about-container {
  display: grid;
  place-items: center;
  box-shadow: 0 0.4em 0.7em #fff;
  padding: 1em;
}

.founder-about {
  text-align: center;
}
