html {
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

:root {
  /* font */
  --nav-font: clamp(1rem, 1.7vw, 1.2rem);
  --text-font: clamp(1.2rem, 2vw, 1.6rem);
  --header-font: clamp(2.3rem, 3.5vw, 3.3rem);
  --subhead-font: clamp(1.6rem, 2.5vw, 2.4rem);
  --title-font: clamp(1.3rem, 2.2vw, 2rem);

  --ex-small-font: 0.7rem;
  --small-font: 1rem;
  --medium-font: 1.5rem;
  --large-font: 2rem;
  --ex-large-font: 2.5rem;

  /* color */
  --background-color: #3332;
  --light-color: #a9a9a9;
  --text-color: #111;
  --primary-black: #1113;
  --secondary-black: #1115;
  --dark-black: #111;
  --header-background: orange;

  /* rgb */
  --dark-rgb: rgb(25, 25, 112, 0.2);
  --light-rgb: rgb(255, 255, 255, 0.1);

  /* transiton */
  --fast-transition: 0.3s;
  --normal-transition: 0.6s;
  --slow-transition: 1s;
}

section {
  padding: 1em;
}

h1 {
  font-size: var(--header-font);
}

h2 {
  font-size: var(--subhead-font);
}

p {
  font-size: var(--text-font);
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

a,
button {
  font-size: var(--nav-font);
  cursor: pointer;
}

body {
  background: linear-gradient(var(--background-color), var(--background-color));
  background-repeat: no-repeat;
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Header */
header.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  background-color: #111;
  color: #fff;
}

div.brand-name-container {
  text-align: center;
  font-style: italic;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  flex: 0.7;
}

.brand-name-wrapper {
  display: flex;
}

.brand-name-wrapper p {
  align-self: flex-end;
}

.brand-name-container small {
  text-decoration: underline;
}

nav.nav-container {
  flex: 1;
}

nav.nav-container ul {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  margin: 2em;
  box-shadow: 0 0.4em 0.7em var(--background-color);
  border-radius: 0.3em;
  gap: 3em;
  padding: 1em;
}
